.mtableContainer {
    font-size: 1.5em;
}

.mtable {
    margin-left: 2.4em;
    border-spacing: 0.1em;
}

.mtable tr td {
    background-color: rgba(234, 29, 213, 0.6);
    width: 1.9em;
    height: 1.9em;
    text-align: center;
    border: 0.05em solid black;
    border-radius: 0.5em;
}

.mtable tr td.selected-cell {
    background-color: rgba(245, 67, 217, 0.25);
    font-weight: bold;
}

.mtable tr td.confirmed-cell {
    background-color: #ea1dd5;
}

.mtable tr:first-child td {
    font-weight: bold;
}

.mtable tr td:first-child {
    font-weight: bold;
}

.actionBar {
    height: 3em;
}

.actionBar .message {
    text-align: center;
    line-height: 3em;
}

.actionBar .startButton {
    display: block;
    text-align: center;
    font-size: inherit;
    height: 2em;
    line-height: 2em;
    width: 10em;
    border: 0.05em solid black;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 0.25em;
    margin-top: 0.65em;
    margin-left: 8.5em;
    background-color: rgba(234, 29, 213, 0.6);
    color: black;
    text-decoration: none;
}

.actionBar .startButton:hover {
    background-color: rgba(245, 67, 217, 0.25);
    color: black;
    text-decoration: none;
}


